html.DisplayPageBody, body.DisplayPageBody, #root.DisplayPageBody {
    
}

.App#DisplayPageBody {
    display: grid;
    grid-template-rows: 2fr 18fr 1fr;
}

main#displayPage {
    background-color: #B9B9B9;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 27%;
    gap: 45px;
}

.music-type {
    background-color: #353535;
    height: 70px;
    width: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.music-type>h1 {
    padding: 5px 0;
    color: #e3e3e3;
    font-weight: bold;
    font-size: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.display-window {
    background-color: #FFFFFF;
    width: 100%;
    height: 100%;
    border-radius: 40px;
    display: grid;
    grid-template-rows: 1fr 7fr 1fr;    
}

.score-title {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 10px;
    font-size: 30px;
    font-weight: bold;
}

.play-bar {
    background-color: #353535;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0 0 40px 40px;
}

.play-bar>button {
    height: 50px;
    width: 50px;
    background-image: url(./static/Play-Button.svg);
    border: none;
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.play-bar>button:hover {
    opacity: 90%;
}


